

$primary-color: #e31e24;
$secondary-color: #fecc00;
$link-color: #0447a3;
$color-grey: #555;
$color-green: rgb(1, 153, 52);
$bg-light: #eee;
$main-padding:15px;


body{background-color: #fff;}
/*==== Comman classes Start ====*/
h1,h2,h3,h4,h5{margin-top: 0px}
h6{font-weight: 600}
h2{font-size: 1.2rem}
.w-100{width: 100%}
.dnone, .d-none{display: none !important}
.text-right{text-align: right !important}
.text-left{text-align: left !important}
.text-center{text-align: center !important}
.text-justify{text-align: justify}
.btn-link{color: $link-color; text-decoration: none}
.m-0{margin:0px !important}
.mt-0{margin-top:0px !important}
.mb-0{margin-bottom:0px !important}
.mb-1{margin-bottom:5px !important}
.mt-1{margin-top:5px !important}
.mb-2{margin-bottom:10px !important}
.mt-2{margin-top:10px !important}
.mb-3{margin-bottom:20px !important}
.mt-3{margin-top:20px !important}
.mt-4{--margin-top:30px !important; margin-top:30px !important}
.p-0{padding: 0px !important}
.p-1{padding: 10px !important}
.p-2{padding: 15px !important}
.p-3{padding: 20px !important}
.pt-0{padding-top: 0px !important}
.pt-1{padding-top: 5px !important}
.pt-2{padding-top: 10px !important}
.pt-3{padding-top: 20px !important}
.pt-4{padding-top: 30px !important}
.pb-0{padding-bottom: 0px !important; --padding-bottom:0px !important}
.pb-2{--padding-bottom: 10px !important; padding-bottom: 10px !important}
.pb-3{--padding-bottom: 20px !important; padding-bottom: 20px !important}
.pb-4{--padding-bottom: 30px !important; padding-bottom: 30px !important}
.pl-main{padding-left: 12px !important;padding-right: 12px !important;
  ion-icon{font-size: 24px; position: relative; margin-top: -8px; top: 7px}
}
ul{padding-left: 16px}
.bg-danger{background: var(--ion-color-danger) !important}
.bg-success{background: var(--ion-color-success) !important}
.bg-warning{background: var(--ion-color-warning) !important; color: #000 !important}
.bg-dark{background: var(--ion-color-dark) !important}
.bg-tertiary{background: var(--ion-color-tertiary) !important}
.bg-orange{background: rgb(226, 101, 17) !important}
.bg-light-grey{background: #eee !important}
.text-success{color: var(--ion-color-success) !important}
.text-warning{color: var(--ion-color-warning) !important; color: #000 !important}
.text-dark{color: var(--ion-color-dark) !important}
.text-tertiary{color: var(--ion-color-tertiary) !important}
.text-orange{color: rgb(226, 101, 17) !important}
.text-danger{color: var(--ion-color-danger) !important}
.text-warning{color: #c7a709 !important}
.border-success{border-color: var(--ion-color-success) !important}
.border-warning{border-color: var(--ion-color-warning) !important; color: #000 !important}
.border-dark{border-color: var(--ion-color-dark) !important}
.border-tertiary{border-color: var(--ion-color-tertiary) !important}
.border-orange{border-color: rgb(226, 101, 17) !important}
.border-danger{border-color: var(--ion-color-danger) !important}
.border-warning{border-color: #c7a709 !important}
.bgsuccesslight, .bg-success-light{background-color: #cfe3bc !important;}

ion-button{--border-radius:5px}
ion-button.disabled{cursor: default; pointer-events: none; opacity: 0.5}
.disabled{cursor: default; pointer-events: none; opacity: 0.5}

ion-item {--padding-end: 0px; --inner-padding-end: 0px; }
ion-back-button{color: $link-color !important}
.searchbar-input.sc-ion-searchbar-ios{font-size: 1rem; padding-inline-end: 0px}
ion-label{
  h2{font-size: 1.05rem !important}
}

.alert-wrapper{--min-width:310px !important;
  .alert-title{margin-bottom: 10px}
}
ion-grid{padding: 0px;
  ion-row{margin:0px -3px;
    ion-col{padding: 0px 3px}
  }
  &.padding-md{
    ion-row{margin:0px -10px;
      ion-col{padding: 0px 10px}
    }    
  }
  &.padding-sm{
    ion-row{margin:0px -7px;
      ion-col{padding: 0px 7px}
    }    
  }
}
ion-slide:focus, ion-slide:active{outline: none}
.swiper-pagination-bullet{height: 8px; width: 8px; background: #000; bottom: 0px;
  &.swiper-pagination-bullet-active{background: var(--ion-color-primary)}
}

.border-radius-50{border-radius: 50% !important}
.img-avatar{width: 4rem; height: 4rem; padding: 5px; margin-right: 3px}


.btn-cont{margin-top: 16px; margin-bottom: 25px;
  &.footerfixed{position: fixed; left: 0; bottom: 0px; width: 100%; margin-bottom: 0px;box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 20%); z-index: 2; background: #fff}
}

@keyframes pulse {
  0% { transform: scale(1); box-shadow: 0 0 0 0  var(--ion-color-success); }
  100% { transform: scale(1); box-shadow: 0 0 0 10px rgba(0, 0, 0,0); }
  
}

@keyframes shake {
  10%, 90% { transform: translate3d(-1px, 0, 0); }
  20%, 80% { transform: translate3d(2px, 0, 0); }
  30%, 50%, 70% { transform: translate3d(-3px, 0, 0); }
  40%, 60% { transform: translate3d(3px, 0, 0); }
}

/*==== Comman classes End ====*/


@mixin focused-label(){ 
  display: inline-block; background: #fff !important; width: auto !important; max-width: unset !important; z-index: 10; opacity: 1 !important; align-self: auto;
}


.form-cont{
  padding: 10px $main-padding;
  .error-outline{display: none}
  .error-outline + ion-input{border-color: $primary-color}
  ion-input, ion-select,ion-datetime{border:1px solid #ccc; border-radius: 5px; padding: 5px !important; padding-left: 52px !important; height: 3rem;
    &:hover{ background: none !important}
  }
  ion-datetime{line-height: 2.3rem}
  ion-label{position: relative; bottom: -2px; background: #fff !important; left: 50px; z-index: 2; padding-left: 5px; opacity: 0.7}
  .item-has-focus{
    ion-label{@include focused-label; bottom: -10px}
  }
  .item-has-value{
    ion-label{@include focused-label; bottom: -10px}
  }
  ion-icon {position: absolute; left: 15px; bottom: 0.7rem; color: $link-color;
    &.icon-right{left: inherit; right: 15px; z-index: 5}
  }
  ion-item{ font-size: 1rem;
    &:focus, &:hover{background: none}    
  }
  ion-item.item-checkbox{ 
    margin-top: 10px;
    ion-label{position: static; font-size: 0.9rem}
    ion-checkbox{margin-right: 00px}
  }
  .error-outline + ion-item ion-input, ion-item.item-has-value.ion-invalid ion-input{border-color: $primary-color}
  .error-outline + ion-item  ion-icon, ion-item.item-has-value.ion-invalid ion-icon{color: $primary-color}
  .error{font-size: 1rem; color: $primary-color; display: block; text-align: right; margin-top: 5px;
    &.error-outline{display: none}
  }

  ion-col{ padding-top: 0px; padding-bottom: 0px; border: 0px !important}
  ion-spinner{margin-left: 5px}
}
.form-cont.no-icon{
  ion-input, ion-select,ion-datetime{padding-left: 15px !important}
  ion-icon{display: none}
  ion-label{left: 10px; color: #444}
}
.otp-cont{
  ion-input{font-size: 1.6rem; text-align: center}
}

.hdng-line{
  position: relative; text-align: center;  font-size: 1.2rem; margin-bottom: 1rem;
  &:before{content: ''; position: absolute; left: 0px; bottom: 47%; width: 100%; height: 2px; background: rgba(0,0,0,0.1); }
  > span {background: #fff; position: relative; z-index: 1; display: inline-block; padding: 7px 20px; /* padding-right: 5px; */ border-radius: 25px; box-shadow: 0px 0px 5px 1px rgb(0 0 0 / 10%); line-height: 22px}
}

.main-cont{
  padding:10px 15px; 
   section + section {margin-top: 1rem; padding-top: 1rem}
  .sec-videos{margin-left: -$main-padding; margin-right: -$main-padding;
    h5{padding-left: 12px}
    figure{ padding-left: 12px; padding-right: 12px;
      img{border-radius: 5px; border: 1px solid #ddd}
    }
  }
  .sec-quick-contact{ background: var(--ion-color-primary-light); margin-left: -15px; margin-right: -15px; padding: 20px 12px;
    ion-col{
      ion-icon{font-size: 3.5rem; }
      h5{margin-top: 0px; margin-bottom: 10px}
      p{margin-bottom: 7px; color: rgba(0,0,0,0.7); font-size: 0.9rem}
      ion-button{text-transform: uppercase}
    }
  }
  h5{font-size: 1.05rem;
    ion-badge{position: relative; bottom: -4px; font-weight: 400;}
  }
}
ion-content{--padding-bottom: 3.6rem}

.sec-video{
  iframe{width: 100%; height: 190px}
  img, ion-img{width: 100%; height: 12rem; object-fit: cover; display: block}
}
.sec-videos{
  iframe{width: 94%; height: 141px; border: 4px solid #ddd; border-radius: 5px}      
  h5{font-size: 1.05rem}    
  ion-slides{padding: 0px 5px}
  ion-slide{
    > div {
      img, ion-img{border-radius: 5px !important; width: 93%; height: 100%; object-fit: cover; border: 1px solid #ddd}
    }    
  }
  img, ion-img{border-radius: 5px !important;border: 1px solid #ddd}
}

.swiper-container-horizontal>.swiper-pagination-bullets{bottom: 0px; position: relative; margin-top: 5px}
.sec-services{ border-bottom: 1px solid #ddd; padding: 1rem; padding-bottom: 0.5rem;
  ion-slide{text-align: center;
    div.router-link-active{
      figure{background: var(--ion-color-secondary-light);
      ion-icon{color: #000}
    }
    }
    div:focus{outline: none}
    h6{font-size: 0.7rem; margin-top: 00px; font-weight: 400; margin: 0px}
  }
  figure{width: 54px; height: 54px; background: #fffbf0; border: 2px solid rgb(254 200 60); position: relative; display: inline-block;
    ion-icon{position: absolute; top: 50%; transform: translateY(-50%); left: 0px; right: 0px; margin:0px auto; font-size: 1.8rem; color: rgba(0,0,0,0.7)}       
  }  
}
.page-welcome{
  .main-cont > section{position: relative;  padding:1.7rem 0px; margin-top: 0px;
    img{border-radius: 2px; border: 1px solid #ddd}
    & + section::before {content: ""; position: absolute; top: 0; left: -15px; height: 12px; width: calc(100% + 30px); background: #eee}
  }  
}
.white-box{
  background-color: #fff; border-radius: 5px; box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.2); overflow: hidden;
  > img {height: 100%; width: 100%; object-fit: cover; display: block }
  &.active{background: var(--ion-color-primary-light);}
}
.grey-box{background: #eee; border: 1px solid rgba(0,0,0,0.2); border-radius: 5px;
  &.secondary-light{background: var(--ion-color-secondary-light)}
  &.primary-light{background: var(--ion-color-primary-light)}
  &.success-light{background: var(--ion-color-success-light)}
  ion-button{background: #fff}
  small{font-size: 80%}
}

.grid-lines{
  h6{margin-bottom: 0px; font-size: 0.9rem}
  img{height: 5rem}
  ion-row{border-top:1px solid #ddd; border-bottom: 1px solid #ddd; }
  ion-col{padding-top: 1.2rem !important; padding-bottom: 1.2rem !important}
  ion-col + ion-col {border-left: 1px solid #ddd}
}

.sec-countries{
  h5{text-align: center}
  img {border-radius: 50%}
figure{
  h6{font-size: 0.7rem; margin-top: 0px; font-weight: 400; margin-bottom: 0px}
}
}

.item-list{
  ion-item{padding-top: 10px; padding-bottom: 10px; border-bottom: 1px solid #ddd;
    ion-thumbnail{width: 4.5rem;margin-right: 5px; padding: 5px}
  }
}

.footer-links{box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2); position: fixed; bottom: 0px; left: 0px; width: 100%; background: #fff; z-index: 10; border-bottom: 1px solid #ddd;
  .main-cont{padding-top: 0px !important; padding-bottom: 0px !important}
  ion-grid{
    ion-row{ text-align: center; margin: 0px -5px;
    ion-col{ padding: 5px;
      ion-label{font-size: 0.7rem; display: block; color: rgba(0,0,0,0.6); font-weight: 500}
      div{
        ion-icon{font-size: 1.8rem; color: #999}
      }
      & + ion-col{border-left: 1px solid rgba(0,0,0,0.1)}
      &.router-link-active{
        ion-icon{color: var(--ion-color-primary)}
      }
    }
  }
  }
  ion-button{font-size: 1rem}
}
footer{border-top:1px solid #ddd}
.item-native::after{background: none !important}

.white-box{background: #fff}

section + section{margin-top: 1rem}
.uni-cont{ background: #eee;
  h3{font-size: 1.1rem; margin-top: 0px; padding-top: 0px; margin-bottom: 10px; padding-bottom: 0px}
  p{font-size: 0.95rem; margin-top: 10px; margin-bottom: 10px}
  .white-box{padding:1rem}
  .img-avatar{width: 100%; height: auto; border-radius: 50%; background-color: #eee}
  ion-col{margin-top: auto; margin-bottom: auto}
  
  .uni-grid{
    h5{margin: 0px; font-size: 0.8rem}
    ion-grid{ padding: 0px; margin: -1rem; overflow: hidden}
    ion-row + ion-row{border-top:1px solid #ddd}
    ion-col{ margin: 0px; text-align: center; text-align: center; padding: 1rem 3px}
    ion-col + ion-col{border-left:1px solid #ddd}
    figure{display: block; margin: 0px; text-align: center; margin-bottom: 12px;
      > img{height:3rem}
    }
    &.ratings{
      ion-col{text-align: left; font-size: 0.9rem; padding:1rem 0.8rem;
      +ion-col{margin: auto 0px;        
        ion-icon{font-size: 1.2rem; font-weight: 700}
      }
      }
      strong{font-weight: 500;
        &.rating:after{font-family: inherit; display: inline-block; content: ''; padding-left: 5px; position: relative; top: -2px}
        &[class^="5"]::after{content: 'Love It'}
        &[class^="4"]::after{content: 'Like It'; }
        &[class^="3"]::after{content: 'It Is Ok'; }
        &[class^="2"]::after{content: 'Dislike It'}
        &[class^="1"]::after{content: 'Hate It'; }
      }
      .rating-col{padding-left: 40px; position: relative;
        .rating-no{position: absolute; left: 10px; top: 17px; font-weight: 500}
        .star{
          > span {position: relative; top: 1px}
        }
      }
    }  
    .thumb-cont{margin-top: 1rem; text-align: center; height: 60px;  width: 60px; background: #ddd; border-radius: 50%; display: inline-block; padding-top: 12px;
      ion-icon{  text-align: center; font-size: 2.5rem; color: #fff} 
      &.success{background: var(--ion-color-success); padding-top: 8px}
      &.danger{background: var(--ion-color-primary)}
    }  
  }
  .uni-logo{width: 100%; height: auto; text-align: center;
    > figure{margin-top: 10px !important;
      img{height: 20px}
    }
  }
  .uni-highlights.closed{
    position: relative;
    &:after{content: "Closed"; font-family: inherit; position: absolute; left: -30px; top: 10px; /* height: 100px; */ width: 100px; background: red; color: #fff; text-align: center; display: inline-block; z-index: 9; padding: 3px; font-size: 0.9rem; transform: rotate( -45deg )}
  }
}
.bullet-list{ list-style-type: none; padding: 0px; margin: 0px; font-size: 0.95rem;
  li{padding-left: 21px; position: relative; margin-bottom: 0.8rem; text-align: justify;
    ion-icon{position: absolute; left: 0px; top: 0px; color: $primary-color; font-size: 1.2rem}
  }
  &.md{ font-size: 1rem;
    li{padding-left: 25px;
      ion-icon{font-size: 1.3rem}
    }
  }
}

.sub-details{font-size: 0.85rem; margin-top: 10px; color: #555;
  strong{font-weight: 500}
  ion-icon{margin-right: 5px; margin-left: 2px; position: relative; bottom: -2px; line-height: 0px; display: inline-block; transform: scale(1.4,1.4)}
}
ion-text{
  > h6{font-size: 1.05rem !important}
}
ion-note{
  padding-right: 0px; margin: 0px;
  ion-icon{font-size: 1.5rem}
}
ion-list{
  > h4{font-size: 1.1rem; margin-top: 5px}
  ion-label.content{ text-overflow: unset !important; overflow: unset !important; white-space: unset !important;
    ion-text{display: block;margin-top: 7px; background: var(--ion-color-primary-light); padding: 10px; border-radius: 5px;
      p{ margin-bottom: 0px; color: #555; font-size: 0.9rem}
  }
  }
}
@mixin afterBg { content: ''; position: absolute; top: 0; left: 0; bottom: 0; right: 0; border-radius: 50%; }

.service-cont{background:$bg-light;
  .white-box{padding:1rem}
  ion-grid{margin: 0px; padding: 0px}
  h4{font-size: 1rem; margin-bottom: 0px; margin-top: 0px; padding: 6px 10px; background: #f0d687; display: block; margin-left: 16px; margin-right: 16px;text-align: center; border-radius: 10px 10px 0px 0px; }
}
.service-list{
  ion-row + ion-row{border-top: 1px solid #ddd; margin-top: 0.6rem; padding-top: 0.6rem}
  ion-row + ion-row.no-border{border-top: 0px; /*margin-top: 3px; padding-top: 3px;*/}
  ion-row.heading{
    h5{padding:7px 10px; margin: 0px; border-radius: 5px; background: #eee; font-size: 0.9rem; text-transform: uppercase}
  }
  ion-row.total{background-color: #faf3dd; margin:0px -1rem;  padding-left: 1rem; padding-right: 1rem; padding-bottom: 0.6rem}
  ion-row.total.final{background-color: #faf3dd; font-size: 1.2rem;
     
  }
  ion-row.total.discount{color: var(--ion-color-success)}
  ion-col{padding-left: 0px; padding-right: 0px; margin: auto 0px}
  h6{font-size: 0.9rem; margin:0px; padding: 0px; font-weight: 500;}
  .price{font-weight: bold; 
    &.old{font-size: 80%; text-decoration: line-through; }
    .icon{font-size: 80%}
    ion-icon{transform: scale(1.3,1.3); position: relative; bottom: -2px}
  }
  ion-avatar{display: block; margin: 0px; width: 100%; padding-right: 10px; height: 100%;
    img{display: block; border-radius: 0px; max-height: 72px}
  }
  .btn{font-size: 0.9rem; min-width: 70px; font-weight: 500;; text-transform: uppercase; padding: 5px 1rem; padding-left: 30px ; position: relative;  transition: ease all 0.3s;
    ion-icon{font-size: 1.2rem; position: absolute; left: 8px; z-index: 10; top: 4px; opacity: 1; }
    &.disabled{opacity: 1; background-color: var(--ion-color-success); border-color: var(--ion-color-success);  transition: ease all 0.3s; animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both; transform: translate3d(0, 0, 0)}        
  }
  &.type3{font-size: 0.9rem; border: 1px solid rgba(0,0,0,0.15); padding: 5px 10px; margin-bottom: 1rem; background: #eee;  border-radius: 5px;
    ion-row + ion-row{margin-top: 4px; padding-top: 4px; border-color: rgba(0,0,0,0.15)}
    ion-row{
        ion-col{position: relative;
            .abs{position: absolute; right: 8px}
        }
      }
    }
}

.service-list.type2{
  h6{margin-bottom: 5px; font-size: 1rem}
  .price{font-size: 1.1rem}
}


.btn{text-decoration: none; border: 1px solid #ddd; background-color: #ddd; display: inline-block; padding: 0.5rem 1rem; border-radius: 20px; color: #000; text-align: center; cursor: pointer; 
  &.btn-solid{background-color: $primary-color; color: #fff}
  &.btn-outline{background: #fff;
    &.primary{color: var(--ion-color-primary); border-color: var(--ion-color-primary)}
    &.secondary{color: var(--ion-color-secondary); border-color: var(--ion-color-secondary)}
    &.success{color: var(--ion-color-success); border-color: var(--ion-color-success)}
  }
  &.btn-successs{background-color: $color-green; color: #fff}
  &.disabled{cursor: default; pointer-events: none; opacity: 0.2}
}

.step-list{ white-space: nowrap; overflow-x: auto;  border-bottom: 1px solid #ddd; margin: 0px; padding:10px 12px;
  > li {display: inline-block; padding:0px 10px; position: relative;    
    &:after{content: ''; height: 2px; width: 100%; background-color: #ddd; position: absolute; left: 0px; top: 10px}
    &.completed{color: var(--ion-color-success);
       a{color: var(--ion-color-success)}      
    }
     a {display: block; text-decoration: none;  font-size: 0.9rem;color:#aaa; padding: 2px 0px; border-radius: 5px; padding-top: 24px;
        > ion-icon{position: absolute; left: 0px; right: 0px; margin: 0px auto; top: 00px; font-size: 20px; z-index: 2}
      &:after{content: ""; position: absolute; top: -1px; left: 0px; right: 0px; margin: 0px auto; background-color: #ffffff; height: 22px; width: 22px; border-radius: 50%; z-index: 1; box-shadow: 0px 0px 3px 0px rgb(0 0 0 / 20%)}
    }
  }
  &.vert{ background: #eee; padding-left: 15px; border: 0px; box-shadow: none;  margin: 0px 15px; border-radius: 15px;
    > li {display: block; padding: 0px; 
      &:after{width: 2px; height: 100%; left: 9px; background: rgba(0,0,0,0.1); z-index: 1}
       a { padding: 9px 10px; padding-left: 30px; font-size: 0.9rem; color: rgba(0,0,0,0.6); font-weight: 500;
        > ion-icon{left: 0px; margin:0px; top: 8px; color: rgba(0,0,0,0.5)}
        &:after{left: 0px;margin:0px;background: rgba(255,255,255,0.7); width: 21px; height: 21px; top: 8px}
      }
      &:last-child:after{display: none}
    
    &.completed{ color: #fff; 
       a{ color: rgba(0,0,0,0.8);
        > ion-icon{color: var(--ion-color-success)}
      }
    }  
  }  
  }
}

ul.border-list{ list-style-type: none; padding: 0px; margin:0px;
  > li {position: relative; display: inline-block;
    & + li{margin-left: 10px; padding-left: 10px;
      &:before{content: ''; height: 100%; width: 1px; background: rgba(0,0,0,0.2); position: absolute; left: 0px; bottom: 0px}
    }  
