.success-checkmark {width: 80px; height: 115px; margin: 0 auto;
  .check-icon {width: 80px; height: 80px; position: relative; border-radius: 50%; box-sizing: content-box; border: 6px solid var(--ion-color-success);
      &::before {top: 3px; left: -2px; width: 30px; transform-origin: 100% 50%; border-radius: 100px 0 0 100px}      
      &::after { top: 0; left: 30px; width: 60px; transform-origin: 0 50%; border-radius: 0 100px 100px 0; animation: rotate-circle 1s infinite; }      
      &::before, &::after {content: ''; height: 100px; position: absolute; background: #FFFFFF; transform: rotate(-45deg)}      
      .icon-line {height: 6px; background-color: var(--ion-color-success); display: block; border-radius: 3px; position: absolute; z-index: 10;          
        &.line-tip { top: 46px; left: 14px; width: 25px; transform: rotate(45deg); animation: icon-line-tip 1s infinite; }          
        &.line-long { top: 38px; right: 8px; width: 47px; transform: rotate(-45deg); animation: icon-line-long 1s infinite; }
      }      
      .icon-circle { top: -6px; left: -6px; z-index: 10; width: 80px; height: 80px; border-radius: 50%; position: absolute; box-sizing: content-box; border: 6px solid rgb(95 165 38 / 59%) }      
      .icon-fix { top: 8px; width: 5px; left: 26px; z-index: 1; height: 85px; position: absolute; transform: rotate(-45deg); background-color: #FFFFFF; }
  }
}

@keyframes rotate-circle {
  0% {
      transform: rotate(-45deg);
  }
  25% {
      transform: rotate(-45deg);
  }
  50% {
      transform: rotate(-405deg);
  }
  100% {
      transform: rotate(-405deg);
  }
}

@keyframes icon-line-tip {
  0% { width: 0; left: 1px; top: 19px; }
  54% { width: 0; left: 1px; top: 19px; }
  70% { width: 50px; left: -8px; top: 37px; }
  84% { width: 17px; left: 21px; top: 48px; }
  100% {width: 25px;left: 14px;top: 45px}
}

@keyframes icon-line-long {
  0% {
      width: 0;
      right: 46px;
      top: 54px;
  }
  65% {
      width: 0;
      right: 46px;
      top: 54px;
  }
  84% {
      width: 55px;
      right: 0px;
      top: 35px;
  }
  100% {
      width: 47px;
      right: 8px;
      top: 38px;
  }
}

